import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import classNames from 'classnames';
import { Row, Button, Col } from 'reactstrap';
import { rgbToHex } from '@coreui/coreui/dist/js/coreui-utilities';
import Slots from './Slots';
import AddSlotModal from './AddSlotModal';
import './SlotStylee.css';

class ThemeView extends Component {
  constructor(props) {
    super(props);

    this.state = {
      bgColor: 'rgb(255, 255, 255)'
    }
  }

  componentDidMount () {
    const elem = ReactDOM.findDOMNode(this).parentNode.firstChild
    const color = window.getComputedStyle(elem).getPropertyValue('background-color')
    this.setState({
      bgColor: color || this.state.bgColor
    })
  }

  render() {

    return (
      <table className="w-100">
        <tbody>
        <tr>
          <td className="text-muted">HEX:</td>
          <td className="font-weight-bold">{ rgbToHex(this.state.bgColor) }</td>
        </tr>
        <tr>
          <td className="text-muted">RGB:</td>
          <td className="font-weight-bold">{ this.state.bgColor }</td>
        </tr>
        </tbody>
      </table>
    )
  }
}

class ThemeColor extends Component {
  // constructor(props) {
  //   super(props);
  // }
  render() {

    // const { className, children, ...attributes } = this.props
    const { className, children } = this.props

    const classes = classNames(className, 'theme-color w-75 rounded mb-3')

    return (
      <Col xl="2" md="4" sm="6" xs="12" className="mb-4">
        <div className={classes} style={{paddingTop: '75%'}}></div>
        {children}
        <ThemeView/>
      </Col>
    )
  }
}

class ParkingSlots extends Component {
  constructor(props) {
    super(props);
    this.state = {
      addModalShow: false
    }
    this.handleExcelImport = this.handleExcelImport.bind(this);
    this.handleRefreshButton = this.handleRefreshButton.bind(this);
  }
  handleExcelImport(e){
    e.preventDefault();
  }

  handleRefreshButton(e){
    e.preventDefault();
    window.location.reload(false);
  }
  render() {
    let addModalClose = () => this.setState({addModalShow : false});
    return (
      <div className="animated fadeIn">
        <div className="card">
          <div className="card-header">
            <div>
                <Button className="btn-pill btn-success" size="sm" onClick = {() => this.setState({addModalShow : true})}>
                    <strong><i class="icon-plus"></i> New Slot </strong>
                </Button>
                <Button className="btn-pill btn-success btn-space" size="sm" onClick = {this.handleExcelImport}>
                    <strong><i class="icon-cloud-upload"></i> Excel import</strong>
                </Button>
                <Button className="btn-pill btn-success btn-space" size="sm" onClick = {this.handleRefreshButton}>
                    <strong><i class="icon-refresh"></i> Refresh</strong>
                </Button>
              </div>
          </div>
          <div className="card-body">
            <Slots/>
          </div>
        </div>
        <AddSlotModal
          show = {this.state.addModalShow}
          onHide = {addModalClose}/>
      </div>
    );
  }
}

export default ParkingSlots;
