import React, { Component } from 'react';
import {Modal, Button,} from 'react-bootstrap'
import {
  Col,
  FormGroup,
  Input,
  Select,
  Label,
  Row,
} from 'reactstrap';
import Server_connections from '../../constants/constants';
// import Spinner from '../../Loaders/Spinner'
// import '../ProductList/ShopStylee.css'

class AddSlotModal extends Component {
    constructor(props) {
      super(props);
      this.state = {
        landlord_code: '',
        slot_name: '',
        slot_status: '',
        parking_status: '',
        message: '',
        landlords: [],
        isLoaded: false
      }
      this.handleAddSlot = this.handleAddSlot.bind(this);
      this.onChange = this.onChange.bind(this);
    }

    onChange(e){
      this.setState({[e.target.name]: e.target.value});
      //console.log({[e.target.name]: e.target.value});
    }

    handleAddSlot(e){
      e.preventDefault();

      const addNewSlot = {   
        landlord_code: this.state.landlord_code,
        slot_name: this.state.slot_name,
        slot_status: this.state.slot_status,
        parking_status: this.state.parking_status,
      };

      //console.log(addNewAmount);

      fetch(Server_connections.ADD_NEW_SLOTS_URL,{
        method: 'POST',
        headers:{
          'Accept': 'application/json',
          'Content-Type': 'application/json'
        },
        body:JSON.stringify(addNewSlot)
      })
      .then(res => res.json())
      .then((result) =>{
        this.setState({message : result.message, isLoaded : true})
        console.log([this.state.message])
      },
      (error) => {
        this.setState({message : 'Failed.. Try again!!'})
      });

    }

    componentDidMount(){
      fetch('')
      .then(res => res.json())
      .then(json => {
        this.setState({
          landlords:  json.landlords,
          isLoaded: true
        })
      });
  }
    
    render() {
        var { landlords} = this.state;

        return (
            <Modal {...this.props} size="sm" aria-labelledby="contained-modal-title-vcenter" centered>
                <Modal.Header closeButton>
                    <Modal.Title id="contained-modal-title-vcenter"><h5>Add New Parking Slot</h5></Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <h5><span className='modal-alert'>{this.state.message }</span></h5>
                    <FormGroup row className="my-0">
                        <Col xs="6">
                            <FormGroup>
                                <Label htmlFor="product-barcode" className="lable">Landlord Ref</Label>
                                <Input type="select" name="landlord_code" value={this.state.landlord_code} onChange={this.onChange}>
                                    <option></option>
                                    {landlords.map((landlord, index) => (
                                    <option key={ index}>{ landlord.landlord_code}</option>
                                  ))}
                                </Input>
                            </FormGroup>
                        </Col>
                        <Col xs="6">
                            <FormGroup>
                                <Label htmlFor="slot_name" className="lable">Enter slot label</Label>
                                <Input type="text" name="slot_name" value={this.state.slot_name} onChange={this.onChange}/>
                            </FormGroup>
                        </Col> 
                    </FormGroup>
                    <FormGroup row className="my-0">
                    <Col xs="12">
                            <FormGroup>
                                <Label htmlFor="slot_status" className="lable">Slot Status</Label>
                                <Input type="select" name="slot_status" value={this.state.slot_status} onChange={this.onChange}>
                                    <option></option>
                                    <option value={this.state.state}>Reserved</option>
                                    <option value={this.state.state}>Not Reserved</option>
                                </Input>
                            </FormGroup>
                        </Col>
                        <Col xs="12">
                            <FormGroup>
                                <Label htmlFor="parking_status" className="lable">Parking Status</Label>
                                <Input type="select" name="parking_status" value={this.state.parking_status} onChange={this.onChange}>
                                    <option></option>
                                    <option value={this.state.state}>Free</option>
                                </Input>
                            </FormGroup>
                        </Col>
                    </FormGroup>
                </Modal.Body>
                <Modal.Footer>
                <div className="d-inline">
                  <Button block color="danger" size="sm" className="btn-pill bg-danger" onClick={this.props.onHide}>Close</Button>
                </div>
                <div className="d-inline">
                  <Button block color="success" size="sm" className="btn-pill bg-success" onClick={this.handleAddSlot}>Save</Button>
                </div>
              </Modal.Footer>
            </Modal>
        )
    }
}

export default AddSlotModal;
